<template>
    <div class="m-publish-box">
        <!--  -->
        <publish-header name="">
            <slot name="header"></slot>
        </publish-header>

        <el-form class="m-publish-post">
            <div class="m-publish-source">
                <el-divider content-position="left"> *</el-divider>
                <el-select
                    class="u-source-id"
                    v-model="post.source_id"
                    filterable
                    remote
                    :disabled="!!post.id"
                    placeholder=""
                    :remote-method="search_handle"
                    :loading="options.loading"
                >
                    <el-option
                        v-for="source in options.sources"
                        :key="source.id"
                        :label="source.Name"
                        :value="source.id"
                    >
                        <div class="m-selector-item">
                            <i class="el-icon-collection u-icon"></i>
                            <span class="u-name" v-text="source.Name"></span>
                        </div>
                    </el-option>
                </el-select>
            </div>

            <div class="m-publish-level">
                <el-divider content-position="left"> *</el-divider>
                <el-rate v-model="post.level" class="u-level"></el-rate>
            </div>

            <div class="m-publish-remark">
                <el-divider content-position="left"> *</el-divider>
                <el-input
                    v-model="post.remark"
                    :maxlength="200"
                    :minlength="1"
                    show-word-limit
                    required
                    placeholder=""
                ></el-input>
            </div>

            <div class="m-publish-content">
                <el-divider content-position="left"> *</el-divider>
                <Tinymce
                    v-model="post.content"
                    :attachmentEnable="true"
                    :resourceEnable="true"
                    :height="400"
                />
            </div>

            <div class="m-publish-commit">
                <el-divider content-position="left"></el-divider>
                <el-button
                    class="u-publish"
                    icon="el-icon-s-promotion"
                    type="primary"
                    @click="toPublish"
                    :disabled="processing"
                ></el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
import header from "@/components/publish_header.vue";
import Tinymce from "@jx3box/jx3box-editor/src/Tinymce";

// 
// import { WikiPost } from "@jx3box/jx3box-common/js/helper";
import { wiki } from "@jx3box/jx3box-common/js/wiki";
import User from "@jx3box/jx3box-common/js/user";
import { get_list } from "../service/quest";

export default {
    name: "quest",
    data() {
        return {
            // - 
            options: {
                sources: null,
            },

            // - 
            post: {
                id: "", // ID
                content: "",
                source_id: "",
                level: 0,
                remark: "",
            },

            // 
            loading: false,
            processing: false,
        };
    },
    computed: {
        client() {
            return this.$store.state.client
        }
    },
    methods: {
        toPublish: function () {
            if (!this.post.source_id) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            if (!this.post.content) {
                this.$message({ message: "", type: "warning" });
                return;
            }

            if (!(this.post.level >= 1 && this.post.level <= 5)) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            if (!this.post.remark) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            this.processing = true;
            /* WikiPost.save({
                type: "quest",
                source_id: this.post.source_id,
                level: this.post.level,
                user_nickname: User.getInfo().name,
                content: this.post.content,
                remark: this.post.remark,
            }) */
            const data = {
                source_id: String(this.post.source_id),
                level: this.post.level,
                user_nickname: User.getInfo().name,
                content: this.post.content,
                remark: this.post.remark,
            }
            wiki.post({ type: 'quest', data: data, client: this.client }, {})
                .then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        this.$message({
                            message: "",
                            type: "success",
                        });
                        setTimeout(() => {
                            this.$router.push({
                                name: "wiki_post",
                                params: { type: "quest" },
                            });
                        }, 500);
                    } else {
                        this.$message({
                            message: `${data.message}`,
                            type: "warning",
                        });
                    }
                })
                .finally(() => {
                    this.processing = false;
                });
        },
        // 
        search_handle(keyword = "") {
            this.loading = true;
            get_list({
                keyword: keyword,
                limit: 10,
            }).then((res) => {
                res = res.data;
                if (res.code === 200) this.options.sources = res.data.data;
                this.loading = false;
            });
        },
    },
    created() {
        // 
        this.search_handle("");
        // IDwatch
        let id = this.$route.params.source_id;
        this.post.source_id = id ? parseInt(id) : null;
    },
    watch: {
        "post.source_id": {
            handler() {
                if (!this.post.source_id) return;
                this.loading = true;
                // WikiPost.newest("quest", this.post.source_id, 0)
                wiki.get({ type: 'quest', id: this.post.source_id }, { supply: 0, client: this.client })
                    .then((res) => {
                        let data = res.data;
                        if (data.code === 200) {
                            // 
                            let post = data.data.post;
                            let source = data.data.source;

                            if (post) {
                                this.post.source_id = parseInt(post.source_id);
                                this.post.level = post.level || 1;
                                this.post.remark = "";
                                let content = post.content;
                                content = content.replace(
                                    /(<p>)?\s* []+\s*(<\/p>)?/gi,
                                    ""
                                );
                                content = content.replace(
                                    /(<p>)?\s* []+\s*(<\/p>)?/gi,
                                    ""
                                );
                                content = content.replace(
                                    /(<p>)?\s*\s*(<\/p>)?/gi,
                                    ""
                                );
                                this.post.content = content;
                            } else {
                                this.post.source_id = this.post.source_id
                                    ? parseInt(this.post.source_id)
                                    : "";
                                this.post.level = 0;
                                this.post.remark = "";
                                this.post.content = "";
                            }

                            if (source) {
                                // 
                                let exist = false;
                                this.options.sources =
                                    this.options.sources || [];
                                for (let index in this.options.sources) {
                                    if (
                                        this.options.sources[index].id ==
                                        source.id
                                    ) {
                                        exist = true;
                                        break;
                                    }
                                }
                                if (!exist) {
                                    source.id = source?.QuestID
                                    source.Name = source?.QuestName
                                    this.options.sources.push(source);
                                }
                            }
                        }
                    })
                    .finally(() => {
                        this.loading = false;
                    });
            },
        },
    },
    components: {
        "publish-header": header,
        Tinymce,
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/quest.less";
</style>
